import React from 'react'
import { NavLink } from 'react-router-dom'
function Navbar() {
  return (
    <div>
       <nav class="order-nav navbar navbar-expand-lg navbar-dark bg-dark">
        
        <NavLink to = "/" className="navbar-brand">Food Order
        </NavLink>
        
        <button class=" navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
      <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav ms-auto">
        <li class="nav-item"><NavLink to = "/" className="a-nav nav-link active">Home</NavLink></li>
        <li class="nav-item"><NavLink to = "/Cart" className="a-nav nav-link active ">Cart</NavLink></li>
        {/* <li class="nav-item"><NavLink to = "/Qr" className="a-nav nav-link active ">Go to Qr page</NavLink></li> */}
        <li className="nav-item">
          <a className="a-nav nav-link active" href="mailto:ayushayushravi@gmail.com">Contact Us</a>
        </li>        
      </ul>
      </div>
    
  </nav>
    </div>
  )
}
// fjkbssjd
export default Navbar
