import React from "react";
import styled from "styled-components";
import { numberFormat } from "./numberFormat";
import { Button } from "./Button";
import { P } from "./P";
import axios from "axios";
import { Arrow } from "./Arrow";
import { VerticalBar } from "./VerticalBar";
import { CartButtons } from "./CartButtons";
import {  useNavigate } from "react-router-dom";



export const CartInfo = ({ cart, increaseQ, decreaseQ, removeFromCart,foundEntry,cartEmpty }) => {
const navigate = useNavigate();
const food = [];
var price=0;
const seatNo= foundEntry.SitNo;
const HallNo=foundEntry.Audi;

function post(item){  
  console.log(item);
food.push(item.name)
price=price+(item.price*item.quantity)
}
// console.log(seatNo);
console.log(seatNo);

function handleOnClick(cart) {
        

    // console.log(cart[0]);
    navigate("/payment",{state: {Id : cart}});
{cart.map((item) => (
post(item)
    ))}
    console.log(food);
    console.log(price);
    const res =axios.post(`http://localhost:2610/cart/Cart/post`,
    {
    foodName:food,
    foodPrice:price,
    seatNo:seatNo,
    HallNo:HallNo,
    paymentStatus:false,
   })
    navigate("/payment",{state: {Id : cart,price : price}});

  }
  const data =JSON.parse(localStorage.getItem('cart'));
  console.log(data);
  console.log("cart info page " +cartEmpty);

const Detail = (item,i)=>{
  return(<DetailColumn key={item.name}>
    <P>
      {item.name} <VerticalBar /> {item.quantity} x {item.price} <Arrow />{" "}
      {numberFormat(item.price * item.quantity)}
    </P>
    <CartButtons
      increaseQ={() => increaseQ(i)}
      decreaseQ={() => decreaseQ(i)}
      removeFromCart={() => removeFromCart(i)}
    />
  </DetailColumn>)
}
    return(
  <>
  {/* {cartEmpty===false ? localStorage.clear('cart'): {data.map((item, i) => (Detail(item ,i)))} } */}
  {cart.map((item, i) => (Detail(item ,i)))}
    <CheckoutButton onClick={() => {handleOnClick(cart);}}>Checkout</CheckoutButton>
  </>
)
}
const DetailColumn = styled.div`
  display: flex;
  flex-flow: column;
  width: 100%;
  padding: 5px 0;
  width: 95%;
  border-bottom: 1px solid black;
`;

const CheckoutButton = styled(Button).attrs(() => ({
  backgroundColor: "darkblue"
}))`
  margin-top: 15px;
  margin-bottom: 15px;
`;
