import React, { useState } from "react";
import OrderProduct from "./Order";
import styled from "styled-components";
import API from "./mockAPI";
import { ListedItems } from "./ListedItems";
import {  useLocation} from 'react-router-dom';
import { FixedCart } from "./FixedCart";
import { CartDetails } from "./CartDetails";
import { Overlay } from "./Overlay";
let foundEntry= {}

export default function Index() {
    const location =useLocation();
    const Code = location.state.Id;
    const [cart, setCart] = useState([]);
    const [items, setItems] = useState(API);
    const [cartOpen, isCartOpen] = useState(false);
    const [carisEmplt,setCartEmply] = useState(true)

  const local =localStorage.getItem('cart')
  // console.log(local);
 
  const addToCart = i => { 

    setItems(state =>
      state.map((item, p) => {
        if (i === p) {
          console.log(p);
          setCart([...cart,{ name: item.name, price: item.price, quantity: item.quantity,src:item.src } ]);

          localStorage.setItem('cart', JSON.stringify(cart));
          return { ...item, inCart: true };
          }
        if (cart.length === 0) {
          setCartEmply(false)
         } 
        return item;
      })
    );  
  };
  const increaseQuantity = {
    inCart: i => {
      setCart(state =>
        state.map((item, o) => {
          if (i === o && item.quantity < 10) {
            return { ...item, quantity: item.quantity + 1 };
          }
          return item;
        })
      );
    },
    inItems: i => {
      setItems(state =>
        state.map((item, o) => {
          if (o === i && item.quantity < 10) {
            return { ...item, quantity: item.quantity + 1 };
          }
          return item;
        })
      );
    }
  };

  const decreaseQuantity = {
    inCart: i => {
      setCart(prevCart =>
        prevCart.map((item, o) => {
          if (i === o && item.quantity > 1) {
            return { ...item, quantity: item.quantity - 1 };
          }
          return item;
        })
      );
    },
    inItems: i => {
      setItems(state =>
        state.map((item, o) => {
          if (i === o && item.quantity > 1) {
            return { ...item, quantity: item.quantity - 1 };
          }
          return item;
        })
      );
    }
  };
  const modifly = (i)=>{
    console.log("click");
  }

  const removeFromCart = i => {
    let chosenItem, index;
    index = 0;
    while (index < cart.length) {
      if (index === i) {
        chosenItem = cart[index].name;
        break;
      }
      index++;
    }
    setCart(state => state.filter(item => chosenItem !== item.name));
    setItems(state =>
      state.map(item => {
        if (item.name === chosenItem) {
          return { ...item, inCart: false, quantity: 1 };
        }
        return item;
      })
    );
  };
  OrderProduct.forEach(entry =>{
        if (entry.id === Code) {
          foundEntry = entry
        }
       })
     

  const cartCountTotal = cart.reduce((acc, item) => acc + item.quantity, 0);
  return (
    <>
      {/* <GlobalStyles /> */}
      <CartDetails
        cartEmply={carisEmplt}
        open={cartOpen}
        onClose={() => isCartOpen(false)}
        cart={cart}
        increaseQ={increaseQuantity.inCart}
        decreaseQ={decreaseQuantity.inCart}
        cartCountTotal={cartCountTotal}
        removeFromCart={removeFromCart}
        foundEntry={foundEntry}
        local={local}
      />

      <FixedCart onOpen={() => isCartOpen(true)} cartItems={cartCountTotal} />
      <Overlay onClick={() => isCartOpen(false)} open={cartOpen} />

      <Wrapper>
        
        <H1>Food menu </H1>
        <H2>Welcome {foundEntry.Name}</H2>
        <H3>Seat No. {foundEntry.SitNo}</H3>
        <H5>Audi:{foundEntry.Audi}</H5>

        <ListedItems
          items={items}
          increaseCount={increaseQuantity.inItems}
          increaseCount2={increaseQuantity.inCart}
          decreaseCount={decreaseQuantity.inCart}
          decreaseCount2={decreaseQuantity.inItems}
          modify ={modifly}
         
          addToCart={addToCart}//hh
          local={local}
        />
      </Wrapper>
    </>
  );
}

const Wrapper = styled.div`
  padding: 75px 0;
  display: flex;
  flex-flow: column;
  align-items: center;
`;
const H1 = styled.h1`
 
  padding: 0 5px 50px 10px;
  text-align: center;
`;
const H2 = styled.h2`
  padding: 0 5px 10px 5px;
  text-align: center;
`;
const H3 = styled.h3`
  padding: 0 15px 10px 5px;
  text-align: left;
`;
const H5 = styled.h5`
  padding: 0 15px 25px 10px;
  text-align: left;
`;
